@import url(https://fonts.googleapis.com/css2?family=Lato&family=Rambla:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&family=Rambla:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&family=Rambla:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&family=Rambla:ital,wght@1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&family=Rambla:ital,wght@1,700&disaply=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

 #last-name {
  align-self: flex-end;
}

.name {
 margin-top: 10%;
 width: 100%;
 display: flex;
 flex-direction: column;
 margin-bottom: 10%;
}


.name-text {
  align-self: flex-start;
  font-family: 'Rambla', sans-serif;
  font-size: 105px;
  text-decoration: none;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-   editor/#eb5777+0,eb5777+33,ef776a+33,ffe143+33,ffe143+66,2fb4e9+66,2aa3d4+100,0c2f3d+100,000000+100 */
  /*text-decoration-color: #eb5777;  Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
  background-image: linear-gradient(to right,  #eb5777 0%,#eb5777 33%,#ef776a 33%,#ffe143 33%,#ffe143 66%,#2fb4e9 66%,#2aa3d4 100%,#0c2f3d 100%,#000000 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#eb5777', endColorstr='#000000',GradientType=1 ); /* IE6-9 */
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: 100% 10px;

}
.sidebar-image {
  max-width: 100%;
  height: auto;
}

.sidebar {
  height: 100%;
  width: 20%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#image{
  border-top: 5px solid black;
  border-bottom: 5px solid black;
  border-left: 5px solid black;
  border-right: 5px solid black;
  border-radius: 10px; 
  max-width: 100%;
  max-height:100%;
}

.stripe {
  margin-left: 2%;
  margin-right:2%;
  width: 5px;
  height: 100%;
}


#Cyan {
  background-color: #00ffff;
}

#Yellow {
  background-color: #ffff00;
}

#Magenta {
  background-color: #ff00ff;
}



@media (max-width: 800px){
  .sidebar {
    width: 90%;
    height: 100vh;
    align-self: center;
  }
  .name {
    margin-bottom: 5vh;
  }
  .sidebar-image {
    height: 40vh;
  }
  .name-text {
    font-size: 105px;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  width: 100%;
  margin-top: 7%;
  margin-bottom: 7%;
}

.nav-button {
  font-family: 'Rambla', sans-serif;
  font-size: 35px;
  text-align: end;
  padding-right: 20px;
  color: black;
  transition: width .25s ease-out;
  -webkit-text-fill-color: white;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}

#about {
  background-color: #F25C84;
}

#publications {
  background-color: #FEE143;
}

#teaching {
  background-color: #2EB3E8;
}
#CV {
  background-color: #000000;
}

a {
  color: inherit;
  text-decoration: none;
}


.nav-button a:visited {
  -webkit-text-fill-color: white;
  -webkit-text-stroke-width: 1px;
  -webkit-text-stroke-color: black;
}

p {
  font-family: 'Lato', sans-serif;
  font-size: 20px;
}

.teaching {
  font-size: 20px;
  font-family: 'Lato', sans-serif;
}

a {
  color: #2EB3E8;
  text-decoration: none;
}
li {
  margin-left: 40px;
}

.main {
    width: 50%;
    display: flex;
    flex-direction: column;
}

@media (max-width: 800px){
  .main {
    align-self: center;
    width: 90%;
  }
}

.num {
  padding-right: 10px;
}

.pub-entry {
  font-family: 'Lato', sans-serf;
  font-size: 20px;
  display: flex;
  flex-direction: row;
}

html, body, .App, #root {
  height: 100vh;
}


.App {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

@media (max-width: 800px){
   html, body, .App, #root {
     height:200vh;
   }
  .App {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
  }  
}

